import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { ProductConsumer } from '../context';
class Product extends Component {
    render() {
        const {id,title,img,price,inCart} = this.props.product;
        return (
            <ProductWraper className="col-9 col-md-6 col-lg-3 mx-auto my-3">
                <div className="card">
                    <ProductConsumer>
                        {
                            (value)=>(
                                <div className="img-container p-5"
                                onClick={()=>value.handleDetail(id)}
                                >
                                <Link to="/details">
                                    <img src={img} className="card-img-top" alt="product-img"></img>
                                </Link>
                                <button 
                                className="cart-btn"
                                 onClick={()=>{value.addToCart(id);
                                    value.openModal(id);
                                 }} disabled={inCart?true:false}>
                                    {
                                        inCart?(<p className="text-capitalize mb-0" disabled>in cart</p>):(<i className="fa fa-cart-plus"></i>)
                                    }
                                </button>
                            </div>
                            )
                        }
                    </ProductConsumer>
                  <div className="card-footer d-flex justify-content-between">
                      <p className="align-self-center mb-0">{title}</p>
                      <h5 className="text-blue font-italic mb-0">
                          <span className="mr-1">
                              ${price}
                          </span>
                      </h5>
                  </div>
                </div>
            </ProductWraper>
        );
    }
}

Product.propTypes={
    product:PropTypes.shape({
        id:PropTypes.number,
        img:PropTypes.string,
        title:PropTypes.string,
        price:PropTypes.number,
        inCart:PropTypes.bool
    }).isRequired
};

const ProductWraper=styled.div`
.card{
    background-color:transparent;
    transiton:all 1s linear;
}
.card-footer{
    background:transparent;
    border-top:transparent;
    transition:all 1s linear;
}
&:hover{
    .card{
        border:0.04rem solid rgba(0,0,0,.2);
        box-shadow:2px 2px 5px 0px rgba(0,0,0,.2);
    }
    .card-footer{
        background:rgba(247,247,247);
    }
}
.img-container{
    position:relative;
    overflow:hidden;
}
.card-img-top{
    transition:all 1s linear;
}
.img-container:hover .card-img-top{
    transform:scale(1.2);
}
.cart-btn{
    position:absolute;
    bottom:0;
    right:0;
    background: var(--lightBlue);
    color: var(--mainWhite);
    border:none;
    font-size:1.4rem;
    border-radius:.5rem 0 0 0;
    transition:all 1s linear;
    transform:translate(100%,100%);
    padding;.2rem .4rem;
}
.img-container:hover .cart-btn{
    transform:translate(0,0);
}
.cart-btn:hover{
    color: var(--mainBlue);
}
`;



export default Product;