import React, { Component } from 'react';
import { ProductConsumer } from '../context';
import {Link} from 'react-router-dom';
import { ButtonContainer } from './Button';

class Details extends Component {
    render() {
        return (
           <ProductConsumer>
               {
                   value=>{
                       const {id,company,img,info,price,title,inCart}=value.detailProduct;
                       return(
                           <div className="container py-5">
                               <div className="row">
                                   <div className="col-10 mx-auto my-5 text-center text-slanted text-blue">
                                       <h1>{title}</h1>
                                   </div>
                               </div>

                               <div className="row">
                                   <div className="col-10 col-md-6 mx-auto my-3">
                                       <img src={img} className="img-fluid" alt="product"></img>
                                   </div>
                                   <div className="col-10 col-md-6 mx-auto my-3 text-capitalize">
                                       <h2>Model: {title}</h2>
                                       <h4 className="text-title text-uppercase text-muted mt-3 mb-2">Made by: <span className="text-uppercase">{company}</span></h4>
                                       <h4 className="text-blue">
                                           <strong>
                                               Price: <span>$</span>
                                               {price} 
                                           </strong>
                                       </h4>
                                       <p className="text-capitalize font-weight-bold mb-0 mt-3">
                                           some info about product:
                                       </p>
                                       <p className="text-muted lead">
                                          {info} 
                                        </p> 
                                        <div>
                                            <Link to="/">
                                                <ButtonContainer>
                                                    Back To Product
                                                </ButtonContainer>
                                            </Link>
                                            <ButtonContainer disabled={inCart?true:false}
                                            cart
                                            onClick={()=>{
                                                value.addToCart(id);
                                                value.openModal(id);
                                            }}
                                            >
                                                {
                                                    inCart?"In Cart":"Add to cart"
                                                }
                                            </ButtonContainer>
                                        </div> 
                                   </div>
                               </div>
                            </div>
                       );
                   }
               }
           </ProductConsumer>
        );
    }
}

export default Details;